*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;

  @media only screen and (max-width: $bp-smallest) {
    font-size: 57%;
  }
}

body {
  font-family: $font-primary;
  font-size: 1.6rem;
  color: $color-secondary;
}



